:root{
  --primary:#d81e5b;
  --secondary: #8c38ff;
  --dark: #131a26;
  --light: #eee;
}
*{
  margin: 0;
  padding: 0;
  box-sizing:border-box ;
  font-family: monospace;
}
body{
  background-color: var(--light);
}
.App{
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  padding: 16px;
}
.calculator{
  width: 100%;
  max-width: 400px;
  background-color: #fff;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0,0,0,0.2);
}
.display{
  padding: 20px;
  text-align: right;
  background-color: var(--dark);
  color: var(--light);
  font-size: 24px;
  font-weight: 300;
}
.display span{
  font-size: 14px;
  color: #888;
}

button{
  appearance: none;
  border: none;
  outline: none;
  color: #fff;
  font-size: 30px;
  cursor: pointer;
  transition: 0.4s;
}
button:hover{
  opacity: 0.9;
}
.operators { 
  display: flex;
}
.operators button{
  flex: 1 1 0%;
  background-color: var(--primary);
  font-weight: 500;
  padding: 15px;
 }
 .digits{
   display: flex;
   flex-wrap: wrap;
 }
 .digits button{
    flex:1 1 33.333%;
    max-width: 5.5cm;
    max-height: 1.5cm;
    background-color: var(--dark);
    padding: 15px;
 }